<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>客户管理</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">客户资料</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>查看客户授信</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="customerInfoAdd">
      <p style="padding-left: 10px;">
        审核状态：{{this.TableData.auditor_status_name}}
      </p>
      <div class="dialog-box">
        <!--<h3>客户基本信息</h3>-->
        <p style="text-align:center;background:#3a8ee6;color:#fff;font-size:14px;font-weight:200;height:40px;line-height:40px;">授信客户信息</p>
        <el-form :model="TableData" ref="TableData" class="table_pad">
          <el-form-item label="销售组织">
            <span>{{TableData.company_name}}</span>
          </el-form-item>
          <el-form-item label="授信客户">
            <span style="display: block;height: 30px;text-overflow: ellipsis;overflow: hidden;padding-left: 30px;">{{TableData.customer_name}}</span>
          </el-form-item>
          <el-form-item label="客户分类">
            <span>{{TableData.customer_type_name}}</span>
          </el-form-item>
          <el-form-item label="授信类型" prop="credit_type_name" class="radio-box">
            <span>{{TableData.credit_type_name}}</span>
          </el-form-item>
          <el-form-item label="有效截止日期" v-show="TableData.credit_type==3">
            <span>{{TableData.credit_end_time}}</span>
          </el-form-item>
          <el-form-item label="授信额度(元)">
            <span> {{TableData.credit_limit|priceFormat}}</span>
          </el-form-item>
          <el-form-item label="备注" prop="remark" class="heightLabel">
            <span style="width: 300px;line-height: 20px;">{{TableData.remark}}</span>
            <!-- <el-input size="small" type="text" v-model="TableData.remark" suffix-icon="el-icon-edit-outline" :maxlength="300" style="width: 250px;resize: none" :disabled="true"></el-input> -->
          </el-form-item>
          <el-form-item label="是否停用" class="radio-box">
            <span v-if="TableData.is_disabled==1">是</span>
            <span v-if="TableData.is_disabled==0">否</span>
          </el-form-item>
        </el-form>
      </div>
      <p class="establish_p">
        <span>操作员：{{TableData.add_user_name}}&nbsp;</span>
        <span>&nbsp;日期：{{TableData.add_time}}</span>
      </p>
    </div>
    <div slot="footer" class="dialog-footer">
      <!--未审核-->
      <template v-if="TableData.auditor_status==1">
        <el-button type="primary" @click="examineFun" v-if="authority.b_pass==1">审 核</el-button>
        <el-button @click="abolishFun" v-if="authority.b_invalid==1">作 废</el-button>
      </template>
      <!--作废-->
      <template v-if="TableData.auditor_status==2 || TableData.auditor_status==3 || (authority.b_invalid!=1 && authority.b_pass!=1)">
        <el-button @click="goBack">返 回</el-button>
      </template>
      <template v-else>
        <el-button @click="goBack">取 消</el-button>
      </template>
      <!--<el-button @click="examineFun" v-show="checkPage=='creditaudit'&&this.TableData.auditor_status_name!='已审核'&&TableData.is_disabled!=1">审 核</el-button>-->
      <!--<el-button @click="abolishFun" v-show="checkPage=='creditaudit'&&this.TableData.auditor_status_name!='已审核'&&this.TableData.auditor_status_name!='作废'">作 废</el-button>-->
      <!--<el-button @click="goBack">返 回</el-button>-->
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "customeCreditmanageSearch",
  data() {
    return {
      authority: {},
      TableData: {},
      checkPage: "",
      credit_id: null
    };
  },
  computed: {
    ...mapGetters(["creditmanageSearch_showGet"])
  },
  methods: {
    ...mapActions([
      "creditmanageDefault_show",
      "creditauditDefault_show",
      "creditmanageIndexACT"
    ]),
    goBack() {
      this.$router.push({
        path: "/custom/creditmanage"
      });
      if (this.checkPage == "creditaudit") {
        this.creditauditDefault_show({
          // commont: "creditauditDefault",
          params: this.creditmanageSearch_showGet.params,
          goPage: this.creditmanageSearch_showGet.goPage,
          pageSize: this.creditmanageSearch_showGet.pageSize
        });
      } else {
        this.creditmanageIndexACT({
          // wz alter
          // commont: "creditmanageDefault",
          params: this.creditmanageSearch_showGet.params,
          goPage: this.creditmanageSearch_showGet.goPage,
          pageSize: this.creditmanageSearch_showGet.pageSize
        });
      }
    },
    examineFun() {
      //审核
      this.$axios({
        method: "post",
        url: this.$api.customAuditorPass,
        data: {
          credit_id: this.credit_id
        }
      }).then(res => {
        if (res.data.code == 200) {
          this.$message({
            message: res.data.data,
            type: "success"
          });
          this.goBack();
        }
      });
    },
    abolishFun() {
      //作废
      this.$axios({
        method: "post",
        url: this.$api.customAuditorInvalid,
        data: {
          credit_id: this.credit_id
        }
      }).then(res => {
        if (res.data.code == 200) {
          this.$message({
            message: res.data.data,
            type: "success"
          });
          this.goBack();
        }
      });
    },
    changeCustomer() {
      //授信客户
      let vm = this;
      this.$axios({
        method: "get",
        url: this.$api.getCustomerInfo,
        params: {
          customer_id: this.TableData.customer_id
        }
      }).then(({ data }) => {
        if (data.code == 200) {
          vm.TableData.customer_type = data.data.customer_type_name;
        }
      });
    }
  },
  computed: {
    ...mapGetters(["creditmanageSearch_showGet"])
  },
  created() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    let vm = this;
    this.$axios({
      method: "get",
      url: this.$api.getCreditInfo,
      params: {
        credit_id: this.creditmanageSearch_showGet.credit_id
      }
    }).then(({ data }) => {
      if (data.code == 200) {
        this.$set(this, "TableData", data.data);
        vm.TableData.is_disabled = "" + data.data.is_disabled + "";
        vm.TableData.credit_type = "" + data.data.credit_type + "";
        vm.TableData.customer_type = data.data.customer_type_name;
      }
    });
    this.checkPage = this.creditmanageSearch_showGet.checkPage;
    this.credit_id = this.creditmanageSearch_showGet.credit_id;
  }
};
</script>

<style lang="less" scoped>
.heightLabel .el-form-item__label {
  line-height: 50px !important;
}

.heightLabel .el-textarea__inner {
  resize: none !important;
  background: none !important;
  margin-left: 5px;
  color: #606266 !important;
}

.table_pad span {
  display: inline-block;
  margin-left: 30px;
}

.table_pad .el-form-item__content .el-select--small {
  padding-left: 30px;
}

.table_pad .el-form-item__content .el-input,
.el-radio-group {
  margin-left: 30px;
}
</style>